const ResponseMessage = props => (
  <div>
    {/* {props.message !== "" && <h1>Your booking has succeeded</h1>} */}
    {props.message !== "" && <h1>{props.message.title}</h1>}
    <p>{props.message.name}</p>
    <p>{props.message.hotel}</p>
    <p>{props.message.arrivalDate}</p>
    <p>{props.message.departureDate}</p>
    <pre>{JSON.stringify(props.message)}</pre>
    <style jsx>{`
      pre, h1 {
        font-family: "Arial";
        padding: 20;
        color: #39f;
      }
    `}</style>
  </div>
);

export default ResponseMessage;
